<template>
    <div>
        <div v-for="(item, index) in optionList" v-if="optionList.length<=len" :key="index">
            <span v-if="statusType" :class="getIconColor(item)" class="ui-state_circle"></span>{{item}}
        </div>
        <el-popover v-if="optionList.length>len"
                    style="cursor: pointer;"
                    placement="left"
                    trigger="hover">
            <div class="hover-box" :style="{'max-width':maxWidth,'max-height':maxHeight}">
                <div v-for="(item, index) in optionList"
                     :key="index">{{item}}
                </div>
            </div>
            <div slot="reference">
                <div v-for="(item, index) in optionList" v-if="index<len" :key="index">
                    <span v-if="statusType" :class="getIconColor(item)" class="ui-state_circle"></span>{{item}}
                </div>
                <div>...</div>
            </div>
        </el-popover>
    </div>
</template>

<script>
import {
    getIconColor
} from '@/services/index';

export default {
    name: 'ComPopover',
    props: {
        option: {
            type: [Array, Object],
            default () {
                return [];
            }
        },
        len: {
            type: String,
            default () {
                return '3';
            }
        },
        statusType: {
            type: Boolean,
            default () {
                return false;
            }
        },
        maxWidth: {
            type: String,
            default () {
                return '250px';
            }
        },
        maxHeight: {
            type: String,
            default () {
                return '250px';
            }
        },
        isReverse: {
            type: Boolean,
            default () {
                return true;
            }
        }
    },
    data () {
        return {
            optionList: []
        };
    },
    created () {
        if (Array.isArray(this.option)) {
            this.optionList = [...this.option];

        } else {
            let arr = [];
            for (let i in  this.option) {
                arr.push(this.option[i]);
            }

            this.optionList = arr.filter((item) => {
                return item !== '' && item;
            });
        }

        if (this.isReverse) {
            this.optionList = this.optionList.reverse();
        }
    },
    methods: {
        getIconColor,
    }
};
</script>

<style scoped>
.hover-box {
    overflow: auto;
}
</style>



